                      "
                      src="https://storage.googleapis.com/opensea-static/opensea-brand/buy-button-blue.png"
                      alt="Buy on OpenSea badge"
                    />
                  </a>
                </div>
                <h2 class>vNFT #{{ id }}</h2>
                <h2>
                  Owned by
                  <Address :address="owner" />
                </h2>
              </div>
              <div v-if="isOwner || isCareTaker" class></div>
            </div>
            <div
              class="rounded flex justify-center items-center border-2 p-4 border-gray-900 flex-col shadow-xl bg-white bg-opacity-50"
            >
              <div class="flex w-full justify-between items-center">
                <div class="text-3xl"></div>

                <div
