                >
                  
                </div>
              </div>
              <div class="flip-box">
                <div class="flip-box-inner">
                  <div class="flip-box-front relative">
                    <img
                      id="dropzone"
                      :src="image"
                      alt="Person"
                      :class="{ shake: inBattle }"
                      class="object-contain mb-6"
                      style="height: 220px; width: 320px"
                    />
                    <span class="" :class="{ drop: inBattle }"></span>
                    <span :class="{ drop: inBattle }"></span>
                    <span :class="{ drop: inBattle }"></span>
                    <span :class="{ drop: inBattle }"></span>
                    <span :class="{ drop: inBattle }"></span>
                  </div>

                  <div class="flip-box-back" id="dropzone2">
                    <p>
                      Gives you {{ currentGem.score }} score and resets TOD to
                      {{ currentGem.days }} days.
                    </p>
                  </div>
                </div>
              </div>

              <p class="my-6 text-lg text-center flex flex-col">
                <span class="text-lg">vNFT #{{ id }}</span>
                <span v-if="isOwner">
                  <span
                    class="field-value block"
                    v-show="!showField('name')"
                    @click="focusField('name')"
                    >{{ name }}</span
                  >
                  <input
                    v-model="name"
                    v-show="showField('name')"
                    id="name"
                    type="text"
                    class="field-value form-control"
                    @focus="focusField('name')"
                    @blur="blurField('name')"
                  />
                </span>
                <span v-else>{{ name }}</span>
              </p>
              <div class="flex -mx-3 text-lg mb-4">
                <div class="px-3 text-center">
                  Level:
                  <span>{{ level }}</span>
                </div>
                <!-- <div class="px-3 text-center">
                  Score
                  <span>{{ score }}</span>
                </div>-->
              </div>
              <div class="flex -mx-3">
                <div class="px-3 text-center text-lg">
                  Time till next reward:
                  <span v-if="isVnftAlive">
                    <countdown
                      :end-time="(lastTimeMined + 60 * 60 * 24) * 1000"
                    >
                      <template v-slot:process="mine">
                        <span>
                          {{
                            `${mine.timeObj.h}:${mine.timeObj.m}:${mine.timeObj.s}`
                          }}
                        </span>
                      </template>
                      <template v-slot:finish>
                        <span>Done!</span>
                      </template>
                    </countdown>
                  </span>
                  <span v-else>Dead</span>
                </div>
                <!-- <div class="px-3 text-center">
                  Time Of Death (TOD)
                  <span v-if="isVnftAlive">
                    <vac :end-time="timeUntilStarving * 1000">
                      <span slot="process" slot-scope="{ timeObj }">
                        {{
                          `${timeObj.d} days and ${timeObj.h}:${timeObj.m}:${timeObj.s}`
                        }}
                      </span>
                      <span slot="finish">Dead!</span>
                    </vac>
                  </span>
                  <span v-else>Dead</span>
                </div>-->
              </div>
              <div class="w-2/3 m-0">
                <button
                  v-if="
                    (isOwner || isCareTaker) && !canMine && hasTokenApproval
                  "
                  type="button"
                  disabled
                  class="cursor-not-allowed w-full border-2 border-gray-900 bg-gray-900 bg-opacity-100 text-white py-2 px-3 mt-6 text-md"
                >
                  Can't mine yet
                </button>
                <button
                  v-if="(isOwner || isCareTaker) && canMine && hasTokenApproval"
                  v-on:click="mine()"
                  class="w-full border-2 border-gray-900 bg-gray-900 bg-opacity-100 hover:text-gray-900 hover:bg-white text-white py-2 px-3 mt-6 text-md"
                >
                  Mine
                  <Number decimals="18" :number="expectedReward" />$muse
                </button>
                <div
                  v-if="
                    !hasTokenApproval && (isOwner || isCareTaker) && isVnftAlive
                  "
                  class="text-sm m-0 flex flex-col justify-center"
                >
                  <!-- <div>You'll need to approve the spending of your $muse token to buy gems.</div> -->
                  <button
                    class="border-2 border-gray-900 bg-gray-900 bg-opacity-100 hover:text-gray-900 hover:bg-white text-white py-2 px-3 mt-6 text-md"
                    v-on:click="approve()"
                    v-tooltip.top-center="
                      `1st Approve our contract to be able to mine muse`
                    "
                  >
                    Click to Approve 1st
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div class="md:px-3 w-full mt-8 md:mt-0 md:w-1/3 flex md:h-64">
            <div
              v-if="isOwner || isCareTaker"
              id="items"
              class="w-full market flex justify-between md:justify-start -m-3 flex-wrap leading-snug"
            >
              <div
                class="focus:outline-none w-1/3 md:w-1/3 p-3 flex flex-col items-center gem"
                v-for="gem in gems"
                v-bind:key="gem.id"
              >
                <img
                  :src="gem.image"
                  class="h-12 item cursor-move"
                  alt
                  :gem_id="gem.id"
                  :gen_score="gem.score"
                  :gem_days="gem.days"
                />
                <div class="flex flex-col text-center py-4">
                  <div class="text-dark mb-2">{{ gem.name }}</div>
                  <div class="text-orange-900">{{ gem.price }} $MUSE</div>
                  <div class="text-sm">+{{ gem.score }} PTS</div>
                  <div class="text-sm">TOD {{ gem.days }} Days</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <ConnectWallet />
  </div>
</template>

<script>
import moment from "moment";
import BigNumber from "bignumber.js";

// @ is an alias to /src
import NFT from "@/components/NFT.vue";
import ConnectWallet from "@/components/ConnectWallet.vue";
import Number from "@/components/Number.vue";
import Address from "@/components/Address.vue";
import ColorPicker from "@/components/tools/ColorPicker.vue";

export default {
  name: "vNFT",
  components: {
    NFT,
    ConnectWallet,
    Number,
    Address,
    ColorPicker,
  },
  data: function () {
    return {
      editField: "",
      name: "",
      description: "",
      addressOrigin: "",
      idOrigin: "",
      isVnftAlive: true,
      timeUntilStarving: 0,
      timeVnftBorn: 0,
      lastTimeMined: 0,
      expectedReward: 0,
      score: 0,
      level: 0,
      id: 0,
      hasTokenApproval: false,
      isOwner: false,
      owner: "",
      currentBalance: 0,
      currentGem: {},
      loader: false,
      isCareTaker: false,
      isForSale: false,
      isLoaded: false,
      rarity: 0,
      hp: 0,
      addons: [],
      image: `https://gallery.verynifty.io/api/image/${this.$route.params.id}.svg`,
      gems: [
        {
          name: "Simple",
          id: 1,
          score: 100,
          days: 2,
          price: 5,
          image: require("@/assets/img/items/1.svg"),
        },
        {
          name: "Boost",
          id: 2,
          score: 190,
          days: 1.5,
          price: 6,
          image: require("@/assets/img/items/2.svg"),
        },
        {
          name: "Rekt",
          id: 3,
          score: 1,
          days: 4,
          price: 20,
          image: require("@/assets/img/items/3.svg"),
        },
        {
          name: "Chad",
          id: 4,
          score: 444,
          days: 1,
          price: 13,
          image: require("@/assets/img/items/4.svg"),
        },
        {
          name: "Rug Pull",
          id: 5,
          score: 100,
          days: 7,
          price: 30,
          image: require("@/assets/img/items/5.svg"),
        },
        {
          name: "Pamp it",
          id: 6,
          score: 3100,
          days: 5,
          price: 100,
          image: require("@/assets/img/items/6.svg"),
        },
      ],
      color: "rgba(71,154,104, 39)",
      inBattle: false,
      challengesUsed: 0,
      bonkedStats: "0/11",
    };
  },
  mounted: async function () {
    this.id = this.$route.params.id;

    const context = this;

    window.addEventListener("load", () => {
      this.setupDragAndDrop(context);
    });

    this.fetchvNFTInfo();

    const forSale = await axios.get(
      `https://gallery.verynifty.io/api/opensea/${this.id}`
    );
    this.isForSale = forSale.data == this.id;
  },
  methods: {
    colorChanged: async function (color) {
      if (this.color == color) {
        return;
      }
      this.color = color;
      console.log(color);
      /* Here come the API call to sign and change color */
      const sign = await web3.personal.sign(
        web3.fromUtf8(this.color),
        web3.eth.coinbase,
        async (obj, signature) => {
          const saveColor = await axios.post(
            "https://gallery.verynifty.io/api/attributes",
            {
              id: this.id,
              color: this.color,
              signature,
              publicAddress: this.$store.state.account,
            }
          );
          console.log(saveColor.data);
        }
      );
    },

    colorUpdated: async function (color) {
      console.log(color);
      function updateQueryStringParameter(uri, key, value) {
        value = value.trim();
        key = key.trim();
        return uri + "?" + key + "=" + value;
      }
      let img_url = this.image.split("?")[0];

      const img = updateQueryStringParameter(img_url, "color1", color);
      this.image = img;
    },
    fatality: async function (e) {
      const tokenId = prompt(
        `You'll get 60% of his points by killing this vNFT #. To which vNFT would you like to give these points?`
      );
      let fatality = await this.$store.state.vNFTContract.methods
        .fatality(this.id, tokenId)
        .send({
          from: this.$store.state.account,
        });
    },
    fetchvNFTInfo: async function () {
      if (this.$store.state.web3 == null) {
        return;
      }
      let infos = await this.$store.state.vNFTContract.methods
        .getVnftInfo(this.id)
        .call();

      // console.log(infos);
      this.hasTokenApproval =
        (
          await this.$store.state.MuseTokenContract.methods
            .allowance(
              this.$store.state.account,
              this.$store.state.contracts.vNFT
            )
            .call()
        ).length >= 18;
      this.expectedReward = infos._expectedReward;
      this.timeUntilStarving = parseInt(infos._timeUntilStarving);

      this.lastTimeMined = parseInt(infos._lastTimeMined);
      this.timeVnftBorn = parseInt(infos._timeVnftBorn);
      this.score = parseInt(infos._score);
      this.level = parseInt(infos._level);
      this.owner = infos._owner;
      this.isVnftAlive = infos._isAlive;
      this.owner = infos._owner;
      this.isOwner =
        this.owner.toLowerCase() == this.$store.state.account.toLowerCase();

      let careTaker = await this.$store.state.vNFTContract.methods
        .careTaker(this.id, infos._owner)
        .call();

      let vnftxData = await this.$store.state.vNFTxContract.methods
        .getVnftInfo(this.id)
        .call();
      this.rarity = parseInt(vnftxData._rarity);
      this.hp = parseInt(vnftxData._hp);

      this.challengesUsed = parseInt(
        await this.$store.state.vNFTxContract.methods
          .challengesUsed(this.id)
          .call()
      );

      const timesAttacked = (
        await this.$store.state.vNFTxContract.methods
          .timesAttacked(this.id)
          .call()
      ).toString();

      this.bonkedStats = `${timesAttacked}/11`;

      this.addons = [];

      let addonNumber = await this.$store.state.vNFTxContract.methods
        .addonsBalanceOf(this.id)
        .call();

      for (let index = 0; index < parseInt(addonNumber); index++) {
        let id = await this.$store.state.vNFTxContract.methods
          .addonsOfNftByIndex(this.id, index)
          .call();
        let addon = await this.$store.state.vNFTxContract.methods
          .addon(id)
          .call();
        addon.id = id;
        this.addons.push(addon);
      }
      try {
        const { data } = await axios.get(
          `https://gallery.verynifty.io/api/${this.id}`
        );
        this.image = data.image_data;
        this.name = data.name == null ? "" : data.name;
        this.description = data.description == null ? "" : data.description;
      } catch (error) {}

      this.isCareTaker = careTaker == this.$store.state.account ? true : false;
      this.isLoaded = true;
      let context = this;
      setTimeout(() => {
        this.setupDragAndDrop(context);
      }, 1000);
      await this.updateBalance();
    },
    mine: async function () {
      try {
        this.loader = this.$loading.show({
          // Optional parameters
          container: this.fullPage ? null : this.$refs.formContainer,
          canCancel: false,
          canCanel: true,
          // onCancel: this.onCancel,
        });
        let minetx = await this.$store.state.vNFTContract.methods
          .claimMiningRewards(this.id)
          .send({
            from: this.$store.state.account,
          });
        console.log(minetx);
        this.fetchvNFTInfo();
        this.loader.hide();

        this.$notify({
          group: "foo",
          title: "Success",
          text: `TX Hash: ${minetx.blockHash}`,
          type: "success",
        });
      } catch (e) {
        this.loader.hide();
        this.$notify({
          group: "foo",
          title: "Error",
          text: `There was an error with the tx`,
          type: "error",
        });
      }
    },

    approve: async function () {
      let max =
        "115792089237316195423570985008687907853269984665640564039457584007913129639935";
      let remove = "0";
      let minetx = await this.$store.state.MuseTokenContract.methods
        .approve(this.$store.state.contracts.vNFT, max)
        .send({
          from: this.$store.state.account,
        });
      this.fetchvNFTInfo();
    },
    feed: async function (id) {
      try {
        this.loader = this.$loading.show({
          // Optional parameters
          container: this.fullPage ? null : this.$refs.formContainer,
          canCancel: false,
          onCancel: this.onCancel,
        });
        if (!this.hasTokenApproval) {
          await this.approve();
        }
        let minetx = await this.$store.state.vNFTContract.methods
          .buyAccesory(this.id, id)
          .send({
            from: this.$store.state.account,
          });
        this.fetchvNFTInfo();

        this.loader.hide();

        this.$notify({
          group: "foo",
          title: "Success",
          text: `TX Hash: ${minetx.blockHash}`,
          type: "success",
        });
      } catch (e) {
        this.loader.hide();
        this.$notify({
          group: "foo",
          title: "Error",
          text: `There was an error with the tx`,
          type: "error",
        });
      }
    },
    updateBalance: async function () {
      let balance = await this.$store.state.MuseTokenContract.methods
        .balanceOf(this.$store.state.account)
        .call();
      this.balance = new BigNumber(balance).shiftedBy(-18).toNumber();
    },
    setupDragAndDrop: async function (context) {
      const containers = document.querySelectorAll("#items");
      const dropzone = document.querySelector(".flip-box .flip-box-inner");

      var click = new Audio(require("../assets/audio/Menu Choice.mp3"));
      var after = new Audio(require("../assets/audio/win_sound.afb9W.wav"));

      var gem = new Draggable.Draggable(containers, {
        draggable: ".item",
      });
      gem.on("drag:start", (e) => {
        let elem = e.source.getAttribute("gem_id");
        for (const gem of this.gems) {
          if (gem.id == parseInt(elem)) {
            this.currentGem = gem;
            // console.log(gem);
          }
        }
        click.play();
        this.updateBalance();
      });

      gem.on("drag:stop", () => {
        //  console.log("stop");
      });

      let over = false;
      gem.on("drag:move", (dragMoveEvent) => {
        if (
          dragMoveEvent.sensorEvent.target.id == "dropzone" ||
          dragMoveEvent.sensorEvent.target.id == "dropzone2"
        ) {
          //  console.log("over dropzone");
          dropzone.classList.add("rotate");
          over = true;
        } else {
          over = false;
        }
      });

      gem.on("mirror:destroy", () => {
        if (over) {
          // console.log("mirror:destroy OVER");
          over = false;
          if (this.currentGem.price <= this.balance) {
            this.feed(this.currentGem.id);
            after.play();
          } else {
            alert("You don't have enought muse to get this gem.");
          }
        }

        if (dropzone.classList.contains("rotate")) {
          dropzone.classList.remove("rotate");
        }
      });

      // tippy tooltips
      tippy("#items", {
        content: "Drag a gem to your vNFT to buy it and get it's beneftis",
      });

      // tippy('.gem', {
      //     content: 'Here you can see the muse it costs, the points it gives and the # of days it lets you stay alive',
      // });
    },
    focusField: function (field) {
      this.editField = field;
    },
    blurField: function (field) {
      this.editField = "";
      if (field == "description") {
        this.saveDescription();
      } else if (field == "name") {
        this.saveName();
      }
    },
    showField: function (field) {
      if (field == "name") {
        return this.name == "" || this.editField == field;
      } else if (field == "description") {
        return this.description == "" || this.editField == field;
      }
    },
    saveName: async function () {
      // const web3 = this.$store.state.web3;
      const sign = await web3.personal.sign(
        web3.fromUtf8(this.name),
        web3.eth.coinbase,
        async (obj, signature) => {
          const saveName = await axios.post(
            "https://gallery.verynifty.io/api/name",
            {
              id: this.id,
              name: this.name,
              signature,
              publicAddress: this.$store.state.account,
            }
          );
          console.log(saveName.data);
        }
      );
    },
    saveDescription: async function () {
      const sign = await web3.personal.sign(
        web3.fromUtf8(this.description),
        web3.eth.coinbase,
        async (obj, signature) => {
          const saveDescription = await axios.post(
            "https://gallery.verynifty.io/api/description",
            {
              id: this.id,
              description: this.description,
              signature,
              publicAddress: this.$store.state.account,
            }
          );
          console.log(saveDescription.data);
        }
      );
    },
    battle: async function () {
      var battle = new Audio(require("../assets/audio/cat.mp3"));

      const opponent = prompt("Which pet would you like to attack?");

      if (battle !== "") {
        battle.play();
        this.inBattle = true;

        setTimeout(() => {
          this.inBattle = false;
        }, 5000);
      }

      let minetx = await this.$store.state.vNFTxContract.methods
        .battle(this.id, opponent)
        .send({
          from: this.$store.state.account,
        });
    },
    cashback: async function () {
      let minetx = await this.$store.state.vNFTxContract.methods
        .cashback(this.id)
        .send({
          from: this.$store.state.account,
        });
      alert("Thank you Milker, your cashback is on the way");
    },
  },
  beforeMount: function () {},
  computed: {
    isConnected: function () {
      return this.$store.state.web3 != null;
    },
    wrappedNFTName: function () {
      return "Original vNFT";
    },
    canMine: function () {
      let nextMining = this.lastTimeMined + 60 * 60 * 24;
      let currentTime = Math.floor(Date.now() / 1000);
      return nextMining < currentTime;
      // return true;
    },
  },
  watch: {
    isConnected: function () {
      this.fetchvNFTInfo();
    },
  },
};
</script>

