<template>
  <div class="p-4 w-1/2 md:w-1/4 text-md" id="item">
    <div
      class="p-3 border-4 border-dark hover:shadow hover:border-dark cursor-pointer border-primary rounded-lg flex flex-col items-center justify-between relative"
    >
      <div v-if="addon.owned != null" class="absolute top-0 right-0 m-2">
        x{{ addon.owned }}
      </div>
      <!-- {{ addon.id }} -->
      <img :src="require(`@/assets/img/addons/${addon.id}.jpg`)" class="" />

      <ul class="text-center flex flex-col space-y-3">
        <li class="mt-4">
          <div class="flex items-center -mx-1 justify-center">
            <div class="px-1">Req HP:</div>
            <div class="px-2">{{ addon.requiredhp }}</div>

            <div class="w-1/3">
              <div class="border rounded border-dark">
                <div
                  class="bg-primary py-2 text-sm"
                  :style="{ width: `${addon.requiredhp}%` }"
                ></div>
              </div>
            </div>
          </div>
        </li>
        <li>Rarity: adds {{ addon.rarity }}</li>
        <li class="text" v-if="toBuy">Super Power: {{ addon.description }}</li>
        <li class="text" v-if="toBuy">
          {{ parseInt(addon.quantity) - parseInt(addon.used) }} left
        </li>
      </ul>

      <button
        v-if="toBuy"
        v-on:click="onBuyClicked()"
        class="btn flex items-center justify-center -mx-1 w-32 focus:outline-none"
      >
        <span class="px-1 inline-block text-lg" to="/play">{{
          addon.price
        }}</span>
        <img src="@/assets/img/Muse LogoMark Green.svg" class="h-3 px-1" />
      </button>
      <p v-if="toBuy && !addon.detachable" class="text-xs mt-2">
        * this object can't be detached from the vNFT
      </p>
      <button
        v-if="toUse && currentHP >= parseInt(addon.requiredhp)"
        v-on:click="onUseClicked()"
        class="btn flex items-center justify-center -mx-1 w-32 focus:outline-none"
      >
        <span class="px-1 inline-block text-lg" to="/play">Attach</span>
      </button>

      <button
        v-if="toUse && currentHP < parseInt(addon.requiredhp)"
        class="btn flex items-center justify-center -mx-1 w-auto focus:outline-none"
      >
        <span class="px-1 inline-block text-lg" to="/play"
          >{{ addon.requiredhp }} HP Required</span
        >
      </button>

      <button
        v-if="toRemove && addon.detachable"
        v-on:click="onRemoveClicked()"
        class="btn flex items-center justify-center -mx-1 w-32 focus:outline-none"
      >
        <span class="px-1 inline-block text-lg" to="/play">Remove</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Addon",
  components: {},
  props: {
    addon: Object,
    toUse: Boolean,
    toRemove: Boolean,
    toBuy: Boolean,
    currentHP: Number,
  },
  data: function () {
    return {};
  },
  methods: {
    onBuyClicked: function () {
      this.$emit("buy-clicked", this.addon.id, this.addon);
    },
    onUseClicked: function () {
      this.$emit("use-clicked", this.addon.id, this.addon);
    },
    onRemoveClicked: function () {
      this.$emit("remove-clicked", this.addon.id, this.addon);
    },
  },
  beforeMount: function () {},
  mounted: function () {},
};
</script>

