import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import vNFT from "../views/vNFT.vue";
import Stake from "../views/Stake2.vue";
import Mint from "../views/Mint.vue";
import Wrap from "../views/Wrap.vue";
import About from "../views/About.vue";
import CareTaker from "../views/CareTaker.vue";
import Leaderboard from "../views/Leaderboard.vue";
import Leaderboard2 from "../views/Leaderboard2.vue";
import ClaimAirdrop from "../views/ClaimAirdrop.vue";
import Branding from "../views/Branding.vue";
import Calculator from "../views/Calculator.vue";
import Killerboard from "../views/Killerboard.vue";
import Lottery from "../views/Lottery.vue";
import Bazaar from "../views/Bazaar.vue";
import Farm from "../views/Farm.vue";
import Race from "../views/Race.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/play",
    name: "Play",
    component: Home,
  },
  {
    path: "/",
    name: "About",
    component: About,
  },
  {
    path: "/stake",
    name: "Stake",
    component: Stake,
  },

  {
    path: "/farm",
    name: "Farm",
    component: Farm,
  },
  {
    path: "/mintold",
    name: "Mint",
    component: Mint,
  },
  {
    path: "/wrap",
    name: "Wrap",
    component: Wrap,
  },
  {
    path: "/lottery",
    name: "Lottery",
    component: Lottery,
  },
  {
    path: "/bazaar",
    name: "Bazaar",
    component: Bazaar,
  },
  {
    path: "/leaderboard",
    name: "Leaderboard",
    component: Leaderboard2,
  },
  {
    path: "/leaderboard2",
    name: "Leaderboard2",
    component: Leaderboard,
  },
  {
    path: "/care-taker",
    name: "Care Taker",
    component: CareTaker,
  },
  {
    path: "/branding",
    name: "Branding",
    component: Branding,
  },
  {
    path: "/calc",
    name: "Calculator",
    component: Calculator,
  },
  {
    path: "/killerboard",
    name: "Killerboard",
    component: Killerboard,
  },
  {
    path: "/rac",
    name: "race",
    component: Race,
  },
  {
    path: "/rac/:id",
    name: "race",
    component: Race,
  },

  {
    path: "/nft/:id",
    name: "vNFT",
    component: vNFT,
  },
  {
    path: "/claim/:id/:key",
    name: "Claim",
    component: ClaimAirdrop,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
