<script>
import JoinRaceModal from "@/components/JoinRaceModal.vue";
import Modal from "@/components/Modal.vue";

var RaceBackground = require("../assets/img/race.svg");
console.log("HELLO", RaceBackground);
var isRaceRunning = false;
var graphics;
var followers;
var path;
var needToLoad = false;
var participantOrder;
var NFTimages;

// @ is an alias to /sr
export default {
  name: "Race",

  components: { JoinRaceModal, Modal },
  data: function () {
    return {
      currentRace: 0,
      raceEnded: false,
      participants: 0,
      prize: 0,
      ticketPrice: 0.1,
      maxParticipants: 6,
      devPercent: 5,
      selectingPet: false,
      ownerNFTs: [],
      racers: [],
      NFTimages: {},
      startTime: 200,
      showModal: false,
      game: null,
      id: null,
    };
  },
  created: function () {
    this.initialize();
  },
  mounted: function () {
    this.id = this.$route.params.id;
    console.log("id", this.id);
  },
  methods: {
    initialize: async function () {
      if (this.$store.state.web3 == null) {
        return false;
      }
      this.currentRace = await this.$store.state.NFTRace.methods
        .currentRace()
        .call();

      this.currentRace = 0;
      this.initializeRace();
      await this.refreshRace();
    },
    enterRace: async function (contract, nftId, type) {
      console.log(contract, nftId);
      let send = await this.$store.state.NFTRace.methods
        .joinRace(contract, nftId, type)
        .send({
          value: "100000000000000000",
          from: this.$store.state.account,
        });
    },
    refreshRace: async function () {
      console.log(this.currentRace);
      let raceInfo = await this.$store.state.NFTRace.methods
        .getRaceInfo(this.currentRace)
        .call();
      console.log(raceInfo);
      this.participants = parseInt(raceInfo._participantsCount);
      this.racers = [];
      for (let index = 0; index < this.participants; index++) {
        let participant = Object.assign({}, raceInfo._participants[index]);
        // This is for testing on localhost
        participant.nftContract = "0x57f0b53926dd62f2e26bc40b30140abea474da94";
        participant.key = participant.nftContract + ":" + participant.nftId;
        participant.score = parseInt(participant.score);
        if (this.NFTimages[participant.key] == null) {
          const call =
            "https://api.opensea.io/api/v1/asset/" +
            participant.nftContract +
            "/" +
            participant.nftId +
            "/";
          let nft = await axios.get(call);
          console.log(nft.data);
          this.NFTimages[participant.key] = nft.data.image_url;
        }
        this.racers.push(participant);
      }
      console.log("UDATED RACEDENEDED", this.raceEnded);
      this.raceEnded = raceInfo._raceEnd != "0";
      console.log("UDATED RACEDENEDED", this.raceEnded);

      if (!this.raceEnded) {
        setTimeout(this.refreshRace, 10000);
      }
    },
    initializeRace: async function () {
      console.log("INITIALIZE GAME");
      var config = {
        type: Phaser.AUTO,
        width: 900,
        height: 600,
        backgroundColor: "#ffffff",
        parent: "game",
        scene: {
          preload: preload,
          create: create,
          update: update,
          startRace: startRace,
        },
      };

      this.game = new Phaser.Game(config);

      function startRace() {}

      function preload() {
        this.load.svg("background", RaceBackground);
      }

      function create() {
        console.log(this);
        this.add.image(0, 0, "background").setOrigin(0, 0);
        graphics = this.add.graphics();
        console.log(graphics);

        path = new Phaser.Curves.Path(50, 500);

        path.splineTo([
          45,
          350,
          45,
          505,
          93,
          552,
          121,
          552,
          191,
          551,
          312,
          557,
          440,
          554,
          807,
          551,
          807,
          451,
          672,
          453,
          664,
          326,
          817,
          326,
          811,
          250,
          664,
          250,
          664,
          135,
          811,
          135,
          811,
          37,
          200,
          37,
          45,
          57,
          45,
          350,
          45,
          505,
          93,
          552,
          121,
          552,
          191,
          551,
          312,
          557,
          440,
          554,
          807,
          551,
          807,
          451,
          672,
          453,
          664,
          326,
          817,
          326,
          811,
          250,
          664,
          250,
          664,
          135,
          811,
          135,
          811,
          37,
          200,
          37,
          45,
          57,
          45,
          350,
          45,
          505,
          93,
          552,
          121,
          552,
          191,
          551,
          312,
          557,
          440,
          554,
          807,
          551,
          807,
          451,
          672,
          453,
          664,
          326,
          817,
          326,
          811,
          250,
          664,
          250,
          664,
          135,
          811,
          135,
          811,
          37,
          200,
          37,
          45,
          57,
          45,
          350,
        ]);
      }

      async function update() {
        graphics.clear();
        graphics.lineStyle(2, 0xffffff, 1);

        // path.draw(graphics);

        if (needToLoad) {
          needToLoad = false;
          for (let index = 0; index < participantOrder.length; index++) {
            this.load.svg(
              participantOrder[index].key,
              NFTimages[participantOrder[index].key]
            );
            await this.load.start();

            console.log(NFTimages[participantOrder[index].key]);
          }

          /* This is hacky to wait for loaded images */
          function sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms));
          }
          await sleep(3000);
          console.log(this.load.totalToLoad);

          followers = this.add.group();

          function getRandomInt(max) {
            return Math.floor(Math.random() * Math.floor(max));
          }

          for (let index = 0; index < participantOrder.length; index++) {
            var ball = followers.create(0, -50, participantOrder[index].key);
            console.log("ball", ball);
            let curves = [
              Smooth(
                [500, 2000, 3200, 3800, 5200, 6500, 7000, 8500, 9000, 10000],
                { scaleTo: [0, 10000] }
              ),
              Smooth(
                [1000, 1800, 2400, 3800, 5000, 6000, 7000, 8500, 9200, 10000],
                { scaleTo: [0, 10000] }
              ),
              Smooth(
                [500, 1400, 3000, 3800, 5000, 6000, 7500, 8500, 9500, 10000],
                { scaleTo: [0, 10000] }
              ),
              Smooth(
                [1000, 1500, 3000, 3800, 5000, 6500, 7000, 8500, 8700, 10000],
                { scaleTo: [0, 10000] }
              ),
              Smooth(
                [1000, 2000, 3000, 3800, 4300, 5900, 7000, 8500, 9000, 10000],
                { scaleTo: [0, 10000] }
              ),
              Smooth(
                [1000, 2000, 3000, 3800, 5000, 6000, 7000, 8500, 9000, 10000],
                { scaleTo: [0, 10000] }
              ),
              Smooth(
                [1000, 2000, 3000, 3800, 5000, 6000, 7000, 8500, 9000, 10000],
                { scaleTo: [0, 10000] }
              ),
              Smooth(
                [1000, 2000, 3000, 3800, 5000, 6000, 7000, 8500, 9000, 10000],
                { scaleTo: [0, 10000] }
              ),
              Smooth(
                [1000, 2000, 3000, 3800, 5000, 6000, 7000, 8500, 9000, 10000],
                { scaleTo: [0, 10000] }
              ),
              Smooth(
                [1000, 2000, 3000, 3800, 5000, 6000, 7000, 8500, 9000, 10000],
                { scaleTo: [0, 10000] }
              ),
            ];
            let curve = curves[index];
            ball.setData("vector", new Phaser.Math.Vector2());
            let width = ball.displayWidth;
            let height = ball.displayHeight;
            let maxSize = 90;
            if (width > height) {
              ball.displayWidth = maxSize;
              ball.displayHeight =
                (ball.displayHeight * ball.displayWidth) / width;
            } else {
              ball.displayHeight = maxSize;
              ball.displayWidth =
                (ball.displayWidth * ball.displayHeight) / height;
            }

            this.tweens.add({
              targets: ball,
              z: 1,
              ease: function (x) {
                return curve(x * 10000) / 10000;
              },
              duration: 36000 + index * 400,
              repeat: 0,
              delay: 2000,
            });
          }
          isRaceRunning = true;
        }
        if (isRaceRunning) {
          console.log("race started");
          var balls = followers.getChildren();

          for (var i = 0; i < balls.length; i++) {
            var t = balls[i].z;
            var vec = balls[i].getData("vector");

            //  The vector is updated in-place
            path.getPoint(t, vec);

            balls[i].setPosition(vec.x, vec.y);

            balls[i].setDepth(balls[i].y);
          }
        }
      }
    },
  },
  computed: {
    isConnected: function () {
      return this.$store.state.web3 != null;
    },
  },
  watch: {
    isConnected: async function () {
      await this.initialize();
    },
    selectingPet: async function () {
      if (!this.selectingPet || this.ownerNFTs.length != 0) {
        return;
      }
      let account = this.$store.state.account;
      const call = `https://api.opensea.io/api/v1/assets?owner=${account}`;
      let nfts = await axios.get(call);
      nfts = nfts.data.assets;

      for (const nft of nfts) {
        console.log(nft);
        let thisNft = {
          image: nft.image_original_url,
          name: nft.name,
          contractName: nft.asset_contract.name,
          idOrigin: nft.token_id,
          contractOrigin: nft.asset_contract.address,
          key: nft.asset_contract.address + ":" + nft.token_id,
        };
        if (this.NFTimages[nft.key] != null) {
          this.NFTimages[nft.key] = this.NFTimages.image;
        }
        if (nft.asset_contract.schema_name == "ERC1155") {
          thisNft.type = 1155;
        } else if (nft.asset_contract.schema_name == "ERC721") {
          thisNft.type = 721;
        } else {
          continue;
        }
        this.ownerNFTs.push(thisNft);
      }
    },

    raceEnded: async function (now, old) {
      if (old != now && now) {
        /* Here we should start everything to display the race running*/
        NFTimages = this.NFTimages;
        participantOrder = this.racers.sort(function (a, b) {
          return b.score - a.score;
        });
        for (let index = 0; index < participantOrder.length; index++) {
          participantOrder[index].position = index + 1;
          participantOrder[index].time = 18 + 0.3 * index;
          console.log("time");
        }
        needToLoad = true;
      }
    },
  },
};
</script>
