<template>
  <div class="md:w-1/3 w-4/5 p-4">
    <div
      class="rounded flex justify-center items-center border-2 p-4 border-gray-900 flex-col shadow-xl bg-white bg-opacity-25"
    >
      <span class="text-lg"
        >Total $muse in the pool: <Number decimals="18" :number="totalInPool"
      /></span>
      <img
        src="@/assets/img/nft/4.svg"
        alt="MonaMonaMona"
        class="object-contain mb-6"
        style="height: 180px"
      />
      <p class="mb-6 text-xl md:text-2xl">Mint vNFT</p>
      <div class="flex -mx-3 text-xl md:text-xl">
        <div class="px-3">
          Your Stake:
          <span><Number decimals="18" :number="stakedAmount" /></span>
        </div>
        <div class="px-3">
          Points Earned: <Number decimals="18" :number="pointsEarned" />
        </div>
      </div>

      <div
        class="flex md:flex-row flex-col md:items-start items-center md:-mx-2 -my-2 py-4 mt-4 text-sm"
      >
        <div class="md:px-2 md:py-0 py-2">
          <button class="btn" v-on:click="approveAndStake()">
            <span v-if="!hasTokenApproval">Approve & Stake</span>
            <span v-else>Stake</span>
          </button>
        </div>

        <div class="md:px-2 md:py-0 py-2" v-if="stakedAmount > 0">
          <button class="btn" @click="unstakeAll()">Unstake All</button>
        </div>
        <button class="btn" @click="unstake()">Unstake</button>
      </div>
      <div class="md:px-2 md:py-0 py-2 mb-4">
        <button class="btn" @click="redeem()">Redeem vNFT</button>
      </div>
    </div>
    <div class="mt-10">
      Every 5 muse staked gets ~1 point a day, every 400 points you can redeem a
      new vNFT, redeeming costs 150 Muse.
    </div>
  </div>
</template>


<script>
// @ is an alias to /src
import Number from "@/components/Number.vue";

export default {
  name: "MintComponent",
  components: {
    Number,
  },
  data: function () {
    return {
      hasTokenApproval: false,
      stakedAmount: "0",
      pointsEarned: "0",
      totalInPool: "0",
    };
  },
  mounted: function () {
    this.initialize();
  },
  created: function () {},
  methods: {
    initialize: async function () {
      if (this.$store.state.web3 == null) {
        return;
      }
      this.checkHasStake();
      this.checkIfTokenApproval();
      this.checkPointsEearned();
      this.checkTotalStaked();
    },
    checkIfTokenApproval: async function () {
      this.hasTokenApproval =
        (
          await this.$store.state.MuseTokenContract.methods
            .allowance(
              this.$store.state.account,
              this.$store.state.contracts.StakeForVnfts2
            )
            .call()
        ).length >= 18;
    },
    approve: async function () {
      let max =
        "115792089237316195423570985008687907853269984665640564039457584007913129639935";
      let remove = "0";
      let minetx = await this.$store.state.MuseTokenContract.methods
        .approve(this.$store.state.contracts.StakeForVnfts2, max)
        .send({
          from: this.$store.state.account,
        });
    },
    approveAndStake: async function () {
      let amount = prompt("How much would you like to stake? (min is 50 muse)");
      //  amount = amount * 10 ** 18;
      amount = BigInt(amount * 10 ** 18);
      if (!this.hasTokenApproval) {
        await this.approve();
      }
      let approveAndStake = await this.$store.state.StakeForVnftsContract2.methods
        .stake(amount.toString())
        .send({
          from: this.$store.state.account,
        });

      this.checkHasStake();
    },
    checkTotalStaked: async function () {
      if (this.$store.state.web3 == null) {
        return;
      }
      let totalStaked = await this.$store.state.StakeForVnftsContract2.methods
        .totalStaked()
        .call();

      // alert(totalStaked);

      this.totalInPool = totalStaked;
    },
    checkHasStake: async function () {
      if (this.$store.state.web3 == null) {
        return;
      }
      let stakeAmount = await this.$store.state.StakeForVnftsContract2.methods
        .balance(this.$store.state.account)
        .call();

      this.stakedAmount = stakeAmount;
    },
    checkPointsEearned: async function () {
      if (this.$store.state.web3 == null) {
        return;
      }
      let pointsEarned = await this.$store.state.StakeForVnftsContract2.methods
        .earned(this.$store.state.account)
        .call();

      this.pointsEarned = pointsEarned;
    },
    unstakeAll: async function () {
      alert("You are going to unstake all your muse.");
      let unstake = await this.$store.state.StakeForVnftsContract2.methods
        .exit()
        .send({
          from: this.$store.state.account,
        });
      this.checkHasStake();
    },
    unstake: async function () {
      let amount = prompt("How much would you like to unstake?");
      //  amount = amount * 10 ** 18;
      amount = BigInt(amount * 10 ** 18);
      let unstake = await this.$store.state.StakeForVnftsContract2.methods
        .withdraw(amount)
        .send({
          from: this.$store.state.account,
        });
      this.checkHasStake();
    },
    redeem: async function () {
      let redeem = await this.$store.state.StakeForVnftsContract2.methods
        .redeem()
        .send({
          from: this.$store.state.account,
        });
      alert("Awesome! you just got a brand new vNFT");
    },
  },
  watch: {
    isConnected: function () {
      this.initialize();
    },
  },
  computed: {
    isConnected: function () {
      return this.$store.state.web3 != null;
    },
  },
};
</script>
