<template>
  <div class="home">
    <div class="text-xl md:text-4xl text-center mt-16 px-4 md:px-0">
      Leaderboard
      <p class="md:text-xl text-lg">
        If a vNFT is dead you'll see a skull by his position. You can claim 60%
        of it's points by being the first that clicks on the skull (costs gas
        for the tx)
      </p>
    </div>
    <div
      class="flex w-full md:flex-row flex-col items-center justify-center md:justify-between md:-mx-3"
    >
      <div class="mt-8 flex px-3 items-center">
        <div
          class="cursor-pointer border-gray-900 rounded"
          @click="toggleLeaderboard('leaderboard')"
          :class="{ 'border-b-4': showLeaderboard }"
        >
          Leaderboard
        </div>
        <div
          class="ml-6 cursor-pointer border-gray-900 rounded"
          @click="toggleLeaderboard"
          :class="{ 'border-b-4': showDeadNfts }"
        >
          Death Valley
        </div>
      </div>

      <div class="mt-8 flex cursor-pointer rounded">
        <t-select
          :options="types"
          v-model="type"
          class="border-dark border rounded px-2 focus:outline-none mr-6"
        />
        <div
          :class="{ ' border-gray-900 border-b-4': showTable }"
          @click="toggleTable"
        >
          <img :src="require('../assets/img/list.svg')" class="h-6" />
        </div>
      </div>
    </div>
    <div
      class="flex -m-3 md:px-0 px-2 justify-center md:justify-start mt-8 flex-wrap overflow-y-hidden overflow-x-auto"
    >
      <div class="w-full" v-if="showTable && showLeaderboard">
        <table class="table-auto w-full border border-gray-900">
          <thead>
            <tr>
              <th class="px-4 py-2">Position</th>

              <th class="px-4 py-2">Image</th>
              <th class="px-4 py-2">Name</th>
              <th class="px-4 py-2">Score</th>
              <th class="px-4 py-2">Level</th>
            </tr>
          </thead>
          <tbody
            class="border border-gray-900"
            v-for="(nft, index) in leaderBoard"
            :key="nft.id"
            v-on:click="clickNFT(nft.id)"
          >
            <NFT
              :contract="nft.contract"
              :id="nft.id"
              :position="index + 1 + perPage * (page - 1)"
              :leaderboard="true"
              :table="true"
            />
          </tbody>
        </table>
      </div>
      <div
        v-if="showLeaderboard && !showTable"
        class="md:w-1/4 w-4/5 p-3"
        v-for="(nft, index) in leaderBoard"
        :key="nft.id"
        v-on:click="clickNFT(nft.id)"
      >
        <NFT
          :contract="nft.contract"
          :id="nft.id"
          :position="index + 1 + perPage * (page - 1)"
          :leaderboard="true"
        />
      </div>

      <t-pagination
        v-if="showLeaderboard"
        :total-items="totalRecords"
        :per-page="perPage"
        v-model="page"
        class="mt-20 w-4/5 m-auot m-0"
      />

      <div
        v-if="!showLeaderboard"
        class="md:w-1/4 w-4/5 p-3"
        v-for="(nft, index) in deadVnfts"
        :key="nft.id"
        v-on:click="clickNFT(nft.id)"
      >
        <NFT
          :contract="nft.contract"
          :id="nft.id"
          :position="index + 1"
          :leaderboard="true"
        />
      </div>
      <div
        class="text-xl mt-8"
        v-if="!deadVnfts.length > 0 && !showLeaderboard"
      >
        Querying the blockchain. If this keeps showing for over 6 minutes, no
        dead vNFTs were found.
      </div>
    </div>
    <p class="mt-8 text-center text-sm">
      This page is using data from our API. You don't have to trust us on data
      and can use the
      <router-link class="underline" to="/leaderboard2"
        >simple gallery</router-link
      >.
    </p>
  </div>
</template>

<script>
// @ is an alias to /src
import NFT from "@/components/NFT.vue";

export default {
  name: "Leaderboard",
  components: {
    NFT,
  },
  data: function () {
    return {
      leaderBoard: [],
      deadVnfts: [],
      showLeaderboard: true,
      showTable: false,
      showDeadNfts: false,
      page: 1,
      perPage: 0,
      totalRecords: 0,
      type: "all",
      types: {
        all: "All",
        "57f0b53926dd62f2e26bc40b30140abea474da94": "vNFT",
        e4605d46fd0b3f8329d936a8b258d69276cba264: "Meme Ltd",
      },
    };
  },
  created: function () {
    this.refreshLeaderBoard();
  },
  methods: {
    changePage: function (pageInfo) {
      console.log(pageInfo);
    },
    clickNFT: function (id) {
      this.$router.push({ name: "vNFT", params: { id: id } });
    },
    refreshLeaderBoard: async function () {
      const result = await axios.get(
        `https://gallery.verynifty.io/api/leaderboard/score/${this.page}?type=${this.type}&perPage=`
      );
      console.log(result);
      this.totalRecords = result.data.pagination.total;
      this.perPage = result.data.pagination.perPage;
      this.leaderBoard = [];
      while (this.leaderBoard.length > 0) {
        this.leaderBoard.pop();
      }
      for (const nft of result.data.data) {
        this.leaderBoard.push(nft);
      }
      const deathresult = await axios.get(
        `https://gallery.verynifty.io/api/leaderboard/starving?type=${this.type}`
      );
      while (this.deadVnfts.length > 0) {
        this.deadVnfts.pop();
      }
      for (const nft of deathresult.data) {
        this.deadVnfts.push(nft);
      }
    },
    toggleLeaderboard: function (tab) {
      if (tab == "leaderboard") {
        this.showLeaderboard = true;
        this.showDeadNfts = false;
      } else {
        this.showLeaderboard = false;
        this.showDeadNfts = true;
      }
    },
    toggleTable: function () {
      this.showTable = !this.showTable;
    },
  },
  beforeMount: function () {
    // this.refreshLeaderBoard();
  },
  mounted: function () {
    this.$store.commit(
      "setBackgroundVideoUrl",
      "https://player.vimeo.com/video/409572571?background=1"
    );
  },
  computed: {},
  watch: {
    page: function () {
      this.refreshLeaderBoard();
    },
    type: function () {
      this.refreshLeaderBoard();
    },
  },
};
</script>
