<template>
  <div
    class="container m-auto text-gray-900 mt-6 text-center leading-loose tracking-wide"
  >
    <h1 class="text-center text-4xl">Add a Care Taker</h1>
    <p class="text-xl mt-2">
      Allow an exteranl wallet or contract to buy gems and claim $MUSE on behalf
      of your vNFT without losing ownership of it, it never leaves your wallet.
    </p>
    <p class="mt-2 text-xl ">
      Removing a care taker wallet can be done permissionless and at any moment.
    </p>
    <p class="p-2 text-xl ">
      You'll still be able to mine and claim rewards for your vNFT if you
      wanted, while having a care taker
    </p>

    <div class="flex flex-col px-6 md:px-0  w-full md:w-1/2 m-0 m-auto mt-6">
      <div class="w-full">
        <div class="text-left text-xl">
          Your vNFT:
        </div>
        <input
          v-model="vnftId"
          type="text"
          placeholder="Your vNFT #"
          class="w-full border-2 border-gray-900 bg-white bg-opacity-100 text-gray-900 py-2 px-3 mt-2 text-md "
        />
      </div>
      <div class="w-full mt-6">
        <div class="text-left text-xl">
          Care Taker Address:
        </div>
        <input
          v-model="careTakerAddress"
          type="text"
          placeholder="Care Taker Address"
          class=" w-full border-2 border-gray-900 bg-white bg-opacity-100 text-gray-900 py-2 px-3 mt-2 text-md "
        />
      </div>
      <div class="flex md:flex-row flex-col -mx-3">
        <div class="px-3 w-full md:w-1/2">
          <button
            @click="addCareTaker()"
            type="button"
            class="hover:bg-white hover:text-gray-900 focus:outline-none w-full border-2 border-gray-900 bg-gray-900 bg-opacity-100 text-white py-2 px-3 mt-6 text-md"
          >
            Add care taker
          </button>
        </div>
        <div class="px-3 w-full md:w-1/2">
          <button
            @click="removeCareTaker()"
            type="button"
            class="focus:outline-none hover:bg-white hover:text-gray-900 w-full border-2 border-gray-900 bg-gray-900 bg-opacity-100 text-white py-2 px-3 mt-6 text-md"
          >
            Remove care takers
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data: function() {
    return {
      vnftId: "",
      careTakerAddress: "",
      loader: false,
    };
  },
  mounted: function() {
    this.$store.commit(
      "setBackgroundVideoUrl",
      "https://player.vimeo.com/video/409572571?background=1"
    );
  },
  methods: {
    addCareTaker: async function() {
      try {
        if (this.vnftId == "" || this.careTakerAddress == "") {
          this.$notify({
            group: "foo",
            title: "Input right parameters",
            text: `Input the vNFT id and care taker address to add a care taker.`,
            type: "error",
          });
        } else {
          this.loader = this.$loading.show({
            // Optional parameters
            container: this.fullPage ? null : this.$refs.formContainer,
            canCancel: false,
            // onCancel: this.onCancel,
          });
          let addcaretaker = await this.$store.state.vNFTContract.methods
            .addCareTaker(this.vnftId, this.careTakerAddress)
            .send({
              from: this.$store.state.account,
            });

          this.loader.hide();

          this.$notify({
            group: "foo",
            title: "Success",
            text: `You added ${this.careTakerAddress} as a care taker for #${this.vnftId}.`,
            type: "success",
          });
        }
      } catch (e) {
        this.loader.hide();
        this.$notify({
          group: "foo",
          title: "Error",
          text: `There was an error with the tx`,
          type: "error",
        });
      }
    },
    removeCareTaker: async function() {
      try {
        if (this.vnftId == "") {
          this.$notify({
            group: "foo",
            title: "Input vNFT id",
            text: `Input the vNFT id to clear care takers from.`,
            type: "error",
          });
        } else {
          this.loader = this.$loading.show({
            // Optional parameters
            container: this.fullPage ? null : this.$refs.formContainer,
            canCancel: false,
            // onCancel: this.onCancel,
          });
          let removecaretaker = await this.$store.state.vNFTContract.methods
            .clearCareTaker(this.vnftId)
            .send({
              from: this.$store.state.account,
            });

          this.loader.hide();

          this.$notify({
            group: "foo",
            title: "Success",
            text: `You cleared all care takers.`,
            type: "success",
          });
        }
      } catch (e) {
        this.loader.hide();
        this.$notify({
          group: "foo",
          title: "Error",
          text: `There was an error with the tx`,
          type: "error",
        });
      }
    },
  },
};
</script>
